/**
*  start  end end  each  map 
*  start  end  step  step  -1   end start  0  end  start  step
* @param {Number} start 
* @param {Number} end 
* @param {Number} step 
*/
export default function range(start, end, step) {
	if (end == null) {
		end = start || 0;
		start = 0;
	}

	if(!step){
		step = end < start ? -1 : 1;
	}

	let length = Math.max(Math.ceil((end - start) / step), 0);
	let range = Array(length);

	for (let i = 0; i < length; i++, start += step) {
		range[i] = start;
	}

	return range;
};


